<template>

  <full-height-layout>
    <h3 slot="layout_title">菜品</h3>

    <div slot="layout_inner_head">

      <h5 class="fw-none mt-2">
        全部
      </h5>

      <freight-view-top></freight-view-top>
    </div>

    <freight-view-body
      :freights="freightList"
      :v-if="freightList.length > 0"
      slot="layout_inner_body"
    ></freight-view-body>

  </full-height-layout>

</template>

<script>

import FullHeightLayout from '@/components/Layout/FullHeightLayout.vue'
import FreightViewTop from './Top/FreightViewTop.vue'

import conn from '@/service/conn'
import FreightViewBody from './Body/FreightViewBody.vue'

export default {
  components: { FullHeightLayout, FreightViewTop, FreightViewBody },
  name: 'FreightView',
  data() {
    return {
      freightList: []
    }
  },
  mounted() {

    conn.freight.getList_Freight().then(res => {
      this.freightList = res
    })
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/listing.sass"

</style>


